/*! MATCHES PAGE */

.matches-container {
  position: relative;
  width: 100%;
  color: $white;
  button.back {
    @include pos(absolute,0,null,null,0);
    background-color: transparent;
    transition: .4s;
    .arrow-icon {
      color: $yellow; }
    &:hover {
      transform: translateX(-25%); } }
  .logo {
    margin: 20px 0 40px;
    text-align: center; }
  .matches {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    padding: 20px;
    box-shadow: 0 0 10px 2px $white;
    border-radius: 5px;
    h1 {
      flex-basis: 100%;
      margin-bottom: 20px;
      text-align: center;
      font-size: 4.2rem; }
    .match {
      position: relative;
      flex-basis: 23%;
      margin: 8px 1% 8px;
      .image-container {
        position: relative;
        min-height: 300px;
        border-radius: 10px;
        overflow: hidden;
        transition: .4s;
        img {
          @include position_center();
          width: 100%;
          height: auto; }
        &:hover {
          transform: scale(1.1);
          filter: brightness(1.2); }
        &::after {
          @include pseudo();
          @include pos(0,null,null,0);
          width: 100%;
          height: 100%;
          background: linear-gradient(transparent 70%,$sheer-black); } }
      h2 {
        @include pos(absolute,null,null,5%,5%); } }
    .no-matches {
      padding: 50px;
      margin: 0 auto;
      font-size: 1.8rem;
      text-align: center;
      p {
        line-height: 150%; } } } }

@media (max-width: 1100px) {
  .matches-container {
    .matches {
      .match {
        .image-container {
          min-height: 220px; } } } } }

@media (max-width: 767px) {
  .matches-container {
    .logo {
      margin: 10px 0 20px; }
    .matches {
      padding: 10px;
      h1 {
        margin-bottom: 10px;
        font-size: 3rem; }
      .match {
        flex-basis: 31%;
        margin: 8px 1%;
        .image-container {
          min-height: 200px; } } } } }

@media (max-width: 532px) {
  .matches-container {
    .logo {
      padding-top: 50px;
      margin-top: 0; }
    .matches {
      h1 {
        font-size: 2.6rem; }
      .match {
        flex-basis: 48%;
        .image-container {
          min-height: 150px; } } } } }
