/*! CARD PAGE*/
.card-wrap {
  display: flex;
  min-height: 90vh;
  flex-direction: column;
  align-items: center;
  justify-content: center; }

.card {
  width: 400px;
  padding: 16px 10px;
  border-radius: 5px;
  background-color: $gray;
  .logo {
    margin: 10px;
    text-align: center;
    .swinder-icon {
      margin-right: 10px; } }
  .person {
    padding: 10px;
    border: 1px solid $sheer-white;
    border-radius: 5px;
    box-shadow: 0 1px 8px $sheer-white;
    background-color: $white;
    overflow: hidden;
    .image-container {
      position: relative;
      height: 400px;
      width: 100%;
      border-radius: 5px;
      overflow: hidden;
      img {
        @include pos(absolute, 0, 0);
        width: 100%;
        z-index: 1; }
      &.like::after {
        color: #76BF93;
        @include pseudo("like"); }
      &.nope::after {
        color: #EA6B4F;
        @include pseudo("nope"); }
      &.like::after, &.nope::after {
        @include position_center();
        padding: 8px 40px;
        border: 6px solid;
        border-radius: 10px;
        font-size: 7rem;
        font-weight: bold;
        letter-spacing: 2px;
        text-transform: uppercase;
        transform: translate(-50%,-50%) rotate(-20deg);
        z-index: 1; } }
    .username {
      position: relative;
      padding: 10px 10px;
      border-radius: 10px;
      font-size: 2.6rem;
      strong {
        margin-right: 14px; }
      span {
        display: inline-block; }
      &::after {
        @include pseudo();
        @include pos(absolute,-10px,null,null,0);
        width: 100%;
        height: 110%;
        border: 1px solid gray;
        border-radius: 10px; }
      button {
        @include pos(absolute, 10px, 10px);
        @include circle(30px);
        color: $yellow;
        background-color: $gray;
        transform: translateY(-170%);
        box-shadow: 0 0 2px $white;
        transition: .4s;
        z-index: 1;
        &:hover {
          transform: translateY(-170%) scale(1.2); } } }
    .about {
      max-height: 0px;
      padding: 0 12px;
      font-size: 1.6rem;
      background-color: $white;
      color: #555;
      opacity: 0;
      transition: .7s linear max-height .7s,.7s linear opacity 0s;
      overflow: hidden;
      .log-info {
        font-size: 1.4rem;
        div {
          line-height: 22px; }
        div:first-child {
          font-weight: bold;
          margin-bottom: 6px; } }
      .pickup-line {
        padding: 10px 0;
        margin: 10px 0;
        border-top: 1px solid #aaa;
        border-bottom: 1px solid #aaa; }
      .features {
        .feature {
          display: flex;
          justify-content: space-between;
          line-height: 140%;
          strong {
            letter-spacing: 1px; } } } }

    .about.show {
      transition: .7s linear max-height 0s,.7s linear opacity .7s;
      opacity: 1;
      max-height: 250px; } } }

@media (max-width: 823px) and (orientation:landscape) {
  .card {
    margin-top: 0;
    padding-top: 0;
    width: 100%;
    .person {
      display: flex;
      justify-content: space-evenly;
      flex-wrap: wrap;
      .image-container {
        height: 220px;
        flex-basis: 40%;
        order: 0;
        &.like::after, &.nope::after {
          padding: 4px 20px;
          border: 4px solid;
          font-size: 5rem; } }
      .username {
        font-size: 2rem;
        flex-basis: 50%;
        align-self: flex-start;
        flex-grow: 1;
        order: 2;
        margin: 0;
        button {
          display: none; }
        &::after {
          top: 10%;
          height: 80%; } }
      .about {
        flex-basis: 50%;
        max-height: 250px;
        opacity: 1;
        order: 1; } } } }

@media (max-width: 532px) {
  .card {
    width: 100%;
    .person {
      .image-container {
        height: 320px;
        &.like::after, &.nope::after {
          padding: 4px 20px;
          border: 4px solid;
          font-size: 5rem; } }
      .username {
        font-size: 2.2rem; } } } }

@media (max-width: 360px) {
  .card {
    margin-top: 0;
    .person {
      .image-container {
        height: 260px;
        &.like::after, &.nope::after {
          font-size: 3rem; } }
      .username {
        margin-bottom: 4px;
        font-size: 1.6rem;
        strong {
          margin-right: 10px; } } } } }
